import Vue from "vue";
import VueRouter from "vue-router";
// import HomePage from "./views/HomePage.vue";
// import PostPage from "./views/PostPage.vue";
// import PortfolioPage from "./views/PortfolioPage.vue";
// import LoginPage from "./views/LoginPage.vue";
// import Login from "./views/Login.vue";
import Register from "./views/Register";
// import Start from "./views/Home.vue";
import NewPage from "./views/NewPage";
import NewsFeed from "./views/NewsFeed.vue";
import Home from "./views/Home.vue";
import MyPage from "./views/MyPage.vue";
import SearchPage from "./views/SearchPage.vue";
import DetailPage from "./views/DetailPage.vue";
import FindPassword from "./views/FindPassword.vue";
import InfoModify from "./views/InfoModify";
import AlarmPage from "./views/AlarmPage";
import CodeCoworkers from "./views/CodeCoworkers";
import Admin from "./views/Admin";
import AdminPost from "./components/AdminPost";
import AdminMember from "./components/AdminMember";
import AdminTag from "./components/AdminTag";
import AdminComment from "./components/AdminComment";
import AdminFollow from "./components/AdminFollow";

Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "home",
		component: Home
	},
	// {
	// 	path: "/post",
	// 	name: "post",
	// 	component: PostPage
	// },
	// {
	// 	path: "/portfolio",
	// 	name: "portfolio",
	// 	component: PortfolioPage
	// },
	// {
	// 	path: "/login",
	// 	name: "login",
	// 	component: Login
	// },
	{
		path: "/register",
		name: "register",
		component: Register
	},
	// {
	// 	path: "/start",
	// 	name: "start",
	// 	component: Start
	// },
	{
		path: "/newsfeed",
		name: "newsfeed",
		component: NewsFeed
		// meta: {
		// 	authRequired: true
		// }
	},
	{
		path: "/mypage/:no",
		name: "mypage",
		component: MyPage,
		props: true
		// meta: {
		// 	authRequired: true
		// }
	},
	{
		path: "/newpage",
		name: "newpage",
		component: NewPage
		// meta: {
		// 	authRequired: true
		// }
	},
	{
		path: "/search",
		name: "search",
		component: SearchPage
		// meta: {
		// 	authRequired: true
		// }
	},
	{
		path: "/detail/:idPost",
		name: "detail",
		component: DetailPage,
		props: true
		// meta: {
		// 	authRequired: true
		// }
	},
	{
		path: "/findpwd",
		name: "findpwd",
		component: FindPassword
	},
	{
		path: "/infoModify",
		name: "infoModify",
		component: InfoModify
	},
	{
		path: "/alarm",
		name: "alarm",
		component: AlarmPage
	},
	{
		path: "/cc",
		name: "codecoworkers",
		component: CodeCoworkers
	},
	{
		path: "/admin",
		name: "admin",
		component: Admin
	},
	{
		path: "/admin/post/:no",
		name: "adminpost",
		component: AdminPost,
		props: true
	},
	{
		path: "/admin/member/:no",
		name: "adminmember",
		component: AdminMember,
		props: true
	},
	{
		path: "/admin/tag/:no",
		name: "admintag",
		component: AdminTag,
		props: true
	},
	{
		path: "/admin/comment/:no",
		name: "admincomment",
		component: AdminComment,
		props: true
	},
	{
		path: "/admin/follow/:no",
		name: "adminfollow",
		component: AdminFollow,
		props: true
	},
	{
		path: "*",
		name: "notfound",
		component: NewsFeed
	}
];

const router = new VueRouter({
	// vue-router    History 
	mode: "history",
	base: process.env.BASE_URL,
	routes
});
router.beforeEach(function(to, from, next) {
	// to:  url   
	if (
		to.matched.some(function(routeInfo) {
			return routeInfo.meta.authRequired;
		})
	) {
		//            
		alert("Login Please!");
		// if (this.$session.has("acessToken")) {
		// 	next();
		// }
		next("/");
	} else {
		console.log("routing success : '" + to.path + "'");
		next(); //  
	}
});
export default router;
